<template>
  <div class="col-sm-12" style="overflow:auto;">
    <div class="tops" v-bind:style="{ height: topheight }">
      <supervisory-list v-ref:queryuser></supervisory-list>
    </div>
    <div v-show="showtotal" v-bind:style="{ height: botheight }">
      <div class="col-sm-2" v-show="showbasic">
        <supervisory-chart :selectdata="selectdata"></supervisory-chart>
      </div>
      <div class="col-sm-9">
        <tabset v-ref:tabs :close="false">
          <tab :header='header'>
            <supervisory-service-control  :selectdata="selectdata" ></supervisory-service-control>
          </tab>
          <tab header='户档案信息'>
            <record-message v-ref:record :selectdata="selectdata" :key="tittle1" :edit = "false"></record-message>
          </tab>
          <tab header='表档案信息'>
            <record-message v-ref:record :selectdata="selectdata" :key="tittle2" :edit = "false"></record-message>
          </tab>
          <tab header='设备信息' v-if="selectdata.f_user_type =='非民用'">
          <equipment-message v-ref:equipment :selectdata="selectdata" :key="tittle3" :edit = "false"></equipment-message>
          </tab>
          <tab header='材料信息'>
            <material-message v-ref:material :selectdata="selectdata" :edit = "false"></material-message>
          </tab>
          <tab header='附件'>
            <upload v-ref:file :blodid = "selectdata.f_process_id" :isusetype = "false" :isremark ="false"
                    :isupload = "true"
                    :takeimg = "true"
                    :edit = "false">
            </upload>
          </tab>
        </tabset>
      </div>
    </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  import  {HttpResetClass} from 'vue-client'
    export default {
      title: '流程监控',
      props: [ 'defname'],
      data() {
        return {
          flagl:false,
          flagg: false,
          showbasic: false,
          showtotal: false,
          header: null,
          selectdata: {},
          botheight: '0%',
          topheight: '100%',
          tittle1:'户档案管理',
          tittle2:'表档案管理',
          tittle3:'设备管理',
          info: '流程监控'
        }
      },
      created(){
        let http = new HttpResetClass()
        http.load('POST','rs/logic/ApplyGetConfigs', {data:this.$login.f}, {resolveMsg: null, rejectMsg: '获取流程文件失败，请联系开发人员'}).then((res)=>{
          Vue.supervisory_vue = Vue.prototype.$supervisory_vue = res.data.workflow_vue
        })
        // console.log("ApplyGetConfigs返回数据：",res.data)
        console.log("$supervisory_vue:"+JSON.stringify(this.$supervisory_vue))
      },
      events: {
        'addactive'() {
          this.$refs.queryuser.$refs.cp.$refs.cri.search()
          this.hidden()
          this.showbasic = true
          this.topheight = '20%'
          this.botheight = '80%'
        },
        'changeheight'() {
          this.show = false
          this.botheight = '0%'
        },
        'check'(val) {
          this.selectdata = ''
          this.selectdata = val
          console.log("节点编号:"+this.selectdata.actdefid,"节点名称:"+this.selectdata.defname)
          this.topheight = '20%'
          this.botheight = '80%'
          this.$refs.queryuser.$refs.cp.$refs.grid.model.rows = [val]
          this.$refs.queryuser.$refs.cp.pager = false
          this.showbasic = true
          let msg = val.defname
          this.header = msg
          this.hidden()
        },
        'selfsearch'() {
          this.$refs.queryuser.$refs.cp.$refs.cri.search()
        },
        'close'() {
          this.hidden()
          this.showtotal = false
          this.topheight = '100%'
          this.botheight = '0%'
          this.$refs.queryuser.$refs.cp.pager = true
        }
      },
      methods: {
        hidden() {
          this.showtotal = true
        },
        close() {
          this.hidden()
          this.topheight = '100%'
          this.botheight = '0%'
        }
      },
      watch: {
        'selectdata'(val) {
          console.log('selectdata:', val)
        },
        'selectdata.f_customer_type'() {
          if (this.selectdata.f_customer_type == '企业') {
            this.flagg = true
          }
        },
        'selectdata.f_apply_type'() {
          if (this.selectdata.f_apply_type=="集体报建" || this.selectdata.f_apply_type=="集体报建流程") {
            this.flagl = true
          }
        }
      },

    }
</script>



